import { useEffect, useState } from "react";
import { Table, Divider } from '@arco-design/web-react';
import Progress from "./Progress";
function List() {
    const [state, setState] = useState([]);
    function fetchJsonData() {
        // fetch("json/stats.json", {
        fetch("http://127.0.0.1:9000/json", {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json"
            }
        })
            .then(r => r.json())
            .then(d => setState(d))
            .catch(e => {
                console.log(e);
            })
    }
    useEffect(() => {
        fetchJsonData();
        const timer = setInterval(() => {
            fetchJsonData();
        }, 2000)
        return () => {
            clearInterval(timer)
        }
    }, [])
    // 字节计算
    function bytesToSize(bytes, precision, si) {
        var ret;
        si = typeof si !== 'undefined' ? si : 0;
        if (si != 0) {
            var megabyte = 1000 * 1000;
            var gigabyte = megabyte * 1000;
            var terabyte = gigabyte * 1000;
        } else {
            var megabyte = 1024 * 1024;
            var gigabyte = megabyte * 1024;
            var terabyte = gigabyte * 1024;
        }

        if ((bytes >= megabyte) && (bytes < gigabyte)) {
            ret = (bytes / megabyte).toFixed(precision) + ' M';

        } else if ((bytes >= gigabyte) && (bytes < terabyte)) {
            ret = (bytes / gigabyte).toFixed(precision) + ' G';

        } else if (bytes >= terabyte) {
            ret = (bytes / terabyte).toFixed(precision) + ' T';

        } else {
            return bytes + ' B';
        }
        if (si != 0) {
            return ret + 'B';
        } else {
            return ret + 'iB';
        }
    }
    // 协议
    function calculateProtocol(record) {
        if (record.online4 && !record.online6) {
            return <Progress text={<small>IPv4</small>} state="success" percent="100" />
        } else if (record.online4 && record.online6) {
            return <Progress text="双栈" state="success" percent="100" />
        } else if (!record.online4 && record.online6) {
            return <Progress text="IPv6" state="success" percent="100" />
        } else {
            return <Progress text="故障" state="danger" percent="100" />
        }
    }
    // 在线时间
    function calculateUptime(record) {
        return <span>{record.uptime + "s"}</span>
    }
    // 负载
    function calculateLoad(record) {
        const avgLoad = ((record.load_1 + record.load_5 + record.load_15) / 3).toFixed(2); 
        return <span>{avgLoad}</span>
    }
    // 网络
    function calculateNetwork(record) {
        var netstr = "";
        if (record.network_rx < 1024 * 1024)
            netstr += (record.network_rx / 1024).toFixed(1) + "K";
        else
            netstr += (record.network_rx / 1024 / 1024).toFixed(1) + "M";
        netstr += " | "
        if (record.network_tx < 1024 * 1024)
            netstr += (record.network_tx / 1024).toFixed(1) + "K";
        else
            netstr += (record.network_tx / 1024 / 1024).toFixed(1) + "M";
        return netstr;
    }
    // 总流量
    function calculateTraffic(record) {
        var trafficstr = "";
        if (record.network_in < 1024 * 1024 * 1024 * 1024)
            trafficstr += (record.network_in / 1024 / 1024 / 1024).toFixed(2) + "G";
        else
            trafficstr += (record.network_in / 1024 / 1024 / 1024 / 1024).toFixed(2) + "T";
        trafficstr += " | "
        if (record.network_out < 1024 * 1024 * 1024 * 1024)
            trafficstr += (record.network_out / 1024 / 1024 / 1024).toFixed(2) + "G";
        else
            trafficstr += (record.network_out / 1024 / 1024 / 1024 / 1024).toFixed(2) + "T";
        return trafficstr;
    }
    // CUP
    function calculateCPU(record) {
        return record.cpu + "%";
        // if (record.cpu >= 90)
        //     return <Progress text={record.cpu + "%"} state="danger" percent={record.cpu} />
        // else if (record.cpu >= 80)
        //     return <Progress text={record.cpu + "%"} state="warning" percent={record.cpu} />
        // else
        //     return <Progress text={record.cpu + "%"} state="success" percent={record.cpu} />
    }
    // 内存Memory
    function calculateMemory(record) {
        var memory = ((record.memory_used / record.memory_total) * 100.0).toFixed(0);
        return memory + "%";
        // if (memory >= 90)
        //     return <Progress text={memory + "%"} state="danger" percent={memory} />
        // else if (memory >= 80)
        //     return <Progress text={memory + "%"} state="warning" percent={memory} />
        // else
        //     return <Progress text={memory + "%"} state="success" percent={memory} />
    }
    // 硬盘HDD
    function calculateHDD(record) {
        var HDD = ((record.hdd_used / record.hdd_total) * 100.0).toFixed(0);
        return HDD + "%";
        // if (HDD >= 90)
        //     return <Progress text={HDD + "%"} state="danger" percent={HDD} />
        // else if (HDD >= 80)
        //     return <Progress text={HDD + "%"} state="warning" percent={HDD} />
        // else
        //     return <Progress text={HDD + "%"} state="success" percent={HDD} />
    }
    // 丢包率
    function calculatePing(record) {
        var PING_10010 = record.ping_10010.toFixed(0);
        var PING_189 = record.ping_189.toFixed(0);
        var PING_10086 = record.ping_10086.toFixed(0);

        if (PING_10010 >= 20 || PING_189 >= 20 || PING_10086 >= 20)
            return <Progress text={PING_10010 + "%💻" + PING_189 + "%💻" + PING_10086 + "%"} state="warning" percent="100" />
        else
            return <Progress text={PING_10010 + "%💻" + PING_189 + "%💻" + PING_10086 + "%"} state="success" percent="100" />
    }
    const columns = [
        {
            title: '协议', render: (col, record) => (
                <>{calculateProtocol(record)}</>
            )
        },
        { title: '节点名', dataIndex: 'name' },
        { title: '系统', dataIndex: 'platform' },
        { title: '位置', dataIndex: 'location' },
        {
            title: '在线时间', render: (col, record) => (
                <>{calculateUptime(record)}</>
            )
        },
        {
            title: '负载', render: (col, record) => (
                <>{calculateLoad(record)}</>
            )
        },
        {
            title: '网络 ↓↑', render: (col, record) => (
                <>{calculateNetwork(record)}</>
            )
        },
        {
            title: '总流量 ↓↑', render: (col, record) => (
                <>{calculateTraffic(record)}</>
            )
        },
        {
            title: '处理器', render: (col, record) => (
                <>{calculateCPU(record)}</>
            )
        },
        {
            title: '内存', render: (col, record) => (
                <>{calculateMemory(record)}</>
            )
        },
        {
            title: '硬盘', render: (col, record) => (
                <>{calculateHDD(record)}</>
            )
        },
        {
            title: '丢包率(CU|CT|CM)',
            render: (col, record) => (
                <>{calculatePing(record)}</>
            )
        },

    ];
    return (
        <div className="list_wrapper">
            <div className="list_box">
                <Table columns={columns}
                    data={state}
                    rowKey="network_in"
                    pagination={false}
                />
            </div>
        </div>
    )
}
export default List;